<template>
  <div class="container py-4">
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link :to="{name: 'UserSettings'}" class="btn btn-light icon-circle me-3">
          <i class="bi bi-chevron-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center">Notifications</h3>
      </div>
    </div>

    <div class="">
      <!-- Notification Item -->
      <div v-for="notification in notifications" :key="notification.id" class="card mb-3 p-3 shadow-sm" style="border-radius: 10px;" @click="markAsRead(notification)">
        <div class="d-flex align-items-center">
          <img :src="notification.image_url" alt="Product" class="me-3" style="width: 60px; height: 60px; border-radius: 8px;">
          <div class="flex-grow-1">
            <h6 class="mb-1">{{ notification.description }}</h6>
            <div class="d-flex align-items-center">
              <span class="text-primary fw-bold me-2">{{ notification.original_price }}</span>
              <!--<span class="text-muted text-decoration-line-through">$260.00</span>-->
            </div>
            <small class="text-muted">{{ notification.created_at }}</small>
            <i :class="notification.read_at ? 'bi bi-check-all' : 'bi bi-check'"></i>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script setup>
import {onMounted, ref} from 'vue';
import {request} from "@/helpers/request";
const notifications = ref([]);

const getNotifications = () => {
  request.get('notifications')
      .then(response => {
        notifications.value = response.data.data;
      })
      .catch(error => {
        console.log(error)
      });
}

const markAsRead = (notification) => {
  request.post('notifications/mark-as-read', {
    id: notification.id
  })
      .then(response => {
        getNotifications();
      })
      .catch(error => {
        console.log(error)
      });
}

onMounted(() => {
  getNotifications();
});
</script>

