<template>
  <div class="container py-5">
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link to="/products/categories" class="btn btn-light me-3">
          <i class="bi bi-arrow-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center">Home | Products | <span class="text-capitalize">{{categorySlug}}</span></h3>
        <span @click.prevent="openFilter" class="input-group-text bg-transparent border-0">
          <i class="bi bi-sliders"></i>
        </span>
      </div>
    </div>

    <div class="row">
      <div class="col-6 col-md-4 col-lg-3 mb-4" v-for="product in products.data" :key="product.id">
        <div class="card product-card h-100">
          <router-link :to="{ name: 'SingleProduct', params: { id: product.id } }" class="text-decoration-none">
            <img :src="product.image_url" class="card-img-top" alt="product.name">
          </router-link>
          <div class="card-body">
            <router-link :to="{ name: 'SingleProduct', params: { id: product.id } }" class="text-decoration-none">
              <h6 class="text-success">{{ product.product_type }}</h6>
              <h5 class="card-title text-dark">{{ product.title }}</h5>
              <p class="card-text">{{ formatPrice(product.price) }}</p>
            </router-link>
          </div>
          <div class="d-flex justify-content-end align-items-center">
            <button class="btn btn-primary plus-btn" @click="openModal(product)">
              <i class="bi bi-plus"></i>
            </button>
          </div>
        </div>
      </div>

      <div class="d-flex justify-content-center">
        <vue-awesome-paginate
            :total-items="productCount"
            :items-per-page="products?.meta?.per_page"
            :max-pages-shown="3"
            v-model="currentPage"
            :onClick="onClickHandlerPagination"
        >
          <template #prev-button>
              <span>
                <i class="bi bi-arrow-left"></i>
              </span>
          </template>

          <template #next-button>
              <span>
                 <i class="bi bi-arrow-right"></i>
              </span>
          </template>
        </vue-awesome-paginate>
      </div>
    </div>
  </div>

  <QuickViewModal
      v-if="selectedProduct"
      :product="selectedProduct"
      @close="selectedProduct = null"
  />

  <FilterModal
      v-if="openProductFilter"
      :categories="categories"
      :brands="brands"
      :colors="colors"
      :sizes="sizes"
      @close="openProductFilter = false"
      @apply="applyFilters"
  />
</template>

<script setup>
import QuickViewModal from "@/views/modal/QuickViewModal.vue";
import {onMounted, ref} from "vue";
import {request} from "@/helpers/request";
import FilterModal from "@/views/modal/FilterModal.vue";
import {VueAwesomePaginate} from "vue-awesome-paginate";
import {useRoute, useRouter} from "vue-router";

const router = useRouter();
const route = useRoute();
const categorySlug = route.params.slug;
const products = ref([]);
const brands = ref([]);
const colors = ref([]);
const sizes = ref([]);
const categories = ref([]);
const sub_categories = ref([]);
const selectedProduct = ref(null);
const productCount = ref(0);
const productMaxPrice = ref(0);
const openProductFilter = ref(false);
const currentPage = ref(route.query.page ?? 1);
const allFilterValues = ref({});

const getProducts = (currentPage) => {
  allFilterValues.value['currentPage'] = currentPage;
  request.get('categories/shop-by-subcategory/' + categorySlug, {
    params: allFilterValues.value
  })
      .then(response => {
        products.value = response.data.data?.products
        brands.value = response.data.data?.brands
        colors.value = response.data.data?.colors
        sizes.value = response.data.data?.sizes
        categories.value = response.data.data?.categories
        sub_categories.value = response.data.data?.sub_categories
        productCount.value = response.data.data?.productCount
        productMaxPrice.value = response.data.data?.productMaxPrice
      })
      .catch(error => {
        console.log(error)
      });
}

function openModal(product) {
  selectedProduct.value = product;
}

function openFilter() {
  openProductFilter.value = true;
}

const applyFilters = (filterValues) => {
  allFilterValues.value = filterValues;
  filterValues['currentPage'] = currentPage.value;
  request.get('products/filters', {
    params: filterValues,
  })
      .then(response => {
        products.value = response.data.data?.products
        openProductFilter.value = false;
      })
      .catch(error => {
        console.log(error)
      });
};

const onClickHandlerPagination = (page) => {
  currentPage.value = page;
  router.push({
    path: route.path,
    query: {
      ...route.query,
      page: page
    }
  })

  getProducts(currentPage.value);
};

onMounted(() => {
  getProducts(currentPage.value);
});

const formatPrice = (price) => {
  return `৳${price.toFixed(2)}`;
};
</script>

