<template>
  <div class="container py-5">
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link to="/" class="btn btn-light me-3">
          <i class="bi bi-arrow-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center">Brands</h3>
      </div>
    </div>

    <div class="row g-3">
      <div class="col-12" v-for="brand in brands" :key="brand.id">
        <router-link :to="{ name: 'ShopByBrand', params: { slug: brand.slug } }" class="category-link">
          <div class="category-card">
            <img :src="brand.image_url" :alt="brand.brand_name">
            <div class="category-info">
              <h5>{{ brand.brand_name }}</h5>
              <p class="product-count">{{ brand.brand_wise_product_count }} products</p>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script setup>

import {request} from "@/helpers/request";
import {onMounted, ref} from "vue";
const brands = ref([]);
const getBrands = () => {
  request.get('brands')
      .then(response => {
        brands.value = response.data.data
      })
      .catch(error => {
        console.log(error)
      });
}

onMounted(() => {
  getBrands();
});
</script>

