<template>
  <div class="container py-5">
    <!-- Cart Header -->
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link :to="{name: 'UserDashboard'}" class="btn btn-light me-3">
          <i class="bi bi-arrow-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center">My Wishlist</h3>
      </div>
    </div>

    <h5 class="mb-2">Products</h5>
    <div class="row align-items-center py-3 border-bottom" v-for="wishlistDetail in wishlistDetails" :key="wishlistDetail.id">
      <div class="col-3 text-center">
        <img :src="wishlistDetail?.product?.product_image_url" class="img-fluid rounded shadow-sm" width="100px" alt="Product Image">
      </div>
      <div class="col-6">
        <h5 class="mb-1 fw-semibold">{{ wishlistDetail?.product?.title }}</h5>
        <p class="text-muted mb-1">Color: {{ wishlistDetail?.color }}</p>
        <p class="text-muted mb-3">Size: {{ wishlistDetail?.size }}</p>
        <div class="d-flex align-items-center">
          <button class="btn btn-sm btn-circle btn-minus" @click="decreaseQuantity(wishlistDetail)">-</button>
          <input type="number" v-model.number="wishlistDetail.qty" class="form-control text-center mx-2" style="width: 60px;" min="1">
          <button class="btn btn-sm btn-circle btn-plus" @click="increaseQuantity(wishlistDetail)">+</button>
        </div>
      </div>
      <div class="col-3 text-end">
        <button class="btn btn-sm mb-2" @click="removeWishlist(wishlistDetail)">
          <i class="bi bi-trash text-danger"></i>
        </button>
        <h6 class="mb-0 fw-bold">৳{{ wishlistDetail?.product?.price.toFixed(2) }}</h6>
      </div>
    </div>
  </div>
</template>

<script setup>
import {computed, onMounted, ref} from 'vue'
import {request} from "@/helpers/request";
import {useStore} from "@/stores/mutations";
import toastr from 'toastr';
import router from "@/router";
const store = useStore()

const wishlistDetails = ref([])

onMounted(() => {
  getWishlistDetails();
})

const getWishlistDetails = () => {
  request.get('wishlist/details')
      .then(response => {
        wishlistDetails.value = response?.data?.data
      })
      .catch(error => {
        console.log(error)
      });
}

const increaseQuantity = (cartData) => {
  cartData.qty++
}

const decreaseQuantity = (cartData) => {
  if (cartData.qty > 1) {
    cartData.qty--
  }
}

const removeWishlist = (wishlist) => {
  request.post('wishlist/remove',{
    id: wishlist?.id
  })
      .then(response => {
        getWishlistDetails();
        toastr.success('Product remove from wishlist', 'Success');
      })
      .catch(error => {
        console.log(error)
      });
}
</script>

<style scoped>
.container {
  max-width: 600px;
}

@media (max-width: 768px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

h5, h6 {
  font-weight: bold;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
</style>