<template>
  <div class="container py-5">
    <!-- Cart Header -->
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link to="/" class="btn btn-light me-3">
          <i class="bi bi-arrow-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center">Check Out</h3>
      </div>
    </div>

    <h5 class="mb-2">Product Details</h5>
    <div class="row align-items-center py-3 border-bottom" v-for="cartData in cartDetails?.cartItems" :key="cartData.id">
      <div class="col-3 text-center">
        <img :src="cartData.options.image" class="img-fluid rounded shadow-sm" width="100px" alt="Product Image">
      </div>
      <div class="col-6">
        <h5 class="mb-1 fw-semibold">{{ cartData.name }}</h5>
        <p class="text-muted mb-1">Color: {{ cartData.options.color }}</p>
        <p class="text-muted mb-3">Size: {{ cartData.options.size }}</p>
        <div class="d-flex align-items-center">
          <button class="btn btn-sm btn-circle btn-minus" @click="decreaseQuantity(cartData)">-</button>
          <input type="number" v-model.number="cartData.qty" class="form-control text-center mx-2" style="width: 60px;" min="1">
          <button class="btn btn-sm btn-circle btn-plus" @click="increaseQuantity(cartData)">+</button>
        </div>
      </div>
      <div class="col-3 text-end">
        <button class="btn btn-sm mb-2" @click="removeCartData(cartData)">
          <i class="bi bi-trash text-danger"></i>
        </button>
        <h6 class="mb-0 fw-bold">৳{{ cartData.price.toFixed(2) }}</h6>
      </div>
    </div>

    <div class="row mt-3">
        <h5 class="mb-2">Shipping Details</h5>
        <div class="col-12">
          <div class="form-group">
            <label for="first_name" class="fw-bold">First Name</label>
            <input type="text" v-model="formData.first_name" class="form-control" id="first_name" placeholder="e.g. Jon">
            <span id="first_name_error" class="text-danger"></span>
          </div>

          <div class="form-group mt-2">
            <label for="last_name" class="fw-bold">Last Name</label>
            <input type="text" v-model="formData.last_name" class="form-control" id="last_name" placeholder="e.g. Doe">
            <span id="last_name_error" class="text-danger"></span>
          </div>

          <div class="form-group mt-2">
            <label for="email" class="fw-bold">Email</label>
            <input type="email" v-model="formData.email_address" class="form-control" id="email" placeholder="e.g. jon@gmail.com">
            <span id="email_error" class="text-danger"></span>
          </div>

          <div class="form-group mt-2">
            <label for="phone" class="fw-bold">Phone</label>
            <input type="text" v-model="formData.phone_number" class="form-control" id="phone" placeholder="e.g. o1xxxxxxxxx">
            <span id="phone_error" class="text-danger"></span>
          </div>

          <div class="form-group mt-2">
            <label for="country_id" class="fw-bold">Country</label>
            <select id="country_id" v-model="formData.country_id" class="form-control" @change="fetchStates">
              <option value="" selected>Select Country</option>
              <option v-for="country in countries" :key="country.id" :value="country.id">{{ country.country_name }}</option>
            </select>
            <span id="country_id_error" class="text-danger"></span>
          </div>

          <div class="form-group mt-2">
            <label for="state_id" class="fw-bold">State</label>
            <select id="state_id" v-model="formData.state_id" class="form-control" @change="fetchCities">
              <option value="" selected>Select State</option>
              <option v-for="state in states" :key="state.id" :value="state.id">{{ state.name }}</option>
            </select>
            <span id="state_id_error" class="text-danger"></span>
          </div>

          <div class="form-group mt-2">
            <label for="city_id" class="fw-bold">City</label>
            <select id="city_id" v-model="formData.city_id" class="form-control">
              <option value="" selected>Select City</option>
              <option v-for="city in cities" :key="city.id" :value="city.id">{{ city.name }}</option>
            </select>
            <span id="city_id_error" class="text-danger"></span>
          </div>

          <div class="form-group mt-2">
            <label for="zip_code" class="fw-bold">Zip Code</label>
            <input type="text" v-model="formData.zip_code" class="form-control" id="zip_code" placeholder="e.g. 1216">
            <span id="zip_code_error" class="text-danger"></span>
          </div>

          <div class="form-group mt-2">
            <label for="area_apartment" class="fw-bold">Apartment</label>
            <input class="form-control" v-model="formData.area_apartment" id="area_apartment" placeholder="e.g. Flat-A">
            <span id="area_apartment_error" class="text-danger"></span>
          </div>

          <div class="form-group mt-2">
            <label for="address" class="fw-bold">Address</label>
            <textarea class="form-control" v-model="formData.address" id="address" rows="3" placeholder="e.g. Road#1, Dhaka, Bangladesh"></textarea>
            <span id="address_error" class="text-danger"></span>
          </div>

          <div class="form-group mt-2">
            <label for="delivery_charge" class="fw-bold">Delivery Charge</label>
            <select id="delivery_charge" v-model="formData.delivery_charge" class="form-control">
              <option value="" selected>Select Delivery Charge</option>
              <option v-for="deliveryCharge in deliveryCharges" :key="deliveryCharge" :value="deliveryCharge.price">{{ deliveryCharge.name }}</option>
            </select>
            <span id="delivery_charge_error" class="text-danger"></span>
          </div>
        </div>
    </div>

    <!-- Coupon Input -->
    <div class="input-group mt-4">
      <input type="text" class="form-control" placeholder="Enter Coupon Code" v-model="formData.coupon_code">
      <button class="btn btn-primary applyCouponBtn" @click="applyCoupon">Apply</button>
      <button v-if="formData.coupon_code.length > 0" class="btn btn-danger remove-coupon" @click="removeCoupon">Remove</button>
    </div>
    <span v-if="couponError" class="text-danger">{{ couponError }}</span>

    <!-- Order Summary -->
    <div class="bg-light p-3 shadow-sm rounded mt-4">
      <p><strong>Order Summary</strong></p>
      <ul class="list-unstyled mb-4">
        <li class="d-flex justify-content-between">
          <span>Total Items</span>
          <span>{{ cartDetails?.total_item }} items</span>
        </li>
        <li class="d-flex justify-content-between mt-2">
          <span>Total Amount</span>
          <span>৳{{ totalAmount }}</span>
        </li>
        <li v-if="formData.discount > 0" class="d-flex justify-content-between mt-2">
          <span>Total Discount</span>
          <span>৳{{ formData.discount }}</span>
        </li>
        <li class="d-flex justify-content-between border-top pt-3 mt-2">
          <span>Total</span>
          <strong>৳{{ discountedPrice || grandTotal }}</strong>
        </li>
      </ul>
      <button @click.prevent="payNow" class="btn btn-success checkoutBtn w-100">Pay Now</button>
    </div>
  </div>
</template>

<script setup>
import {computed, onMounted, reactive, ref} from 'vue'
import {request} from "@/helpers/request";
import {useStore} from "@/stores/mutations";
import toastr from 'toastr';
const store = useStore()
import {useRouter} from "vue-router";
const router = useRouter();

const cartDetails = ref([])
const countries = ref([])
const deliveryCharges = ref([])
const states = ref([]);
const cities = ref([]);
const discountedPrice = ref(0)
const couponError = ref('')

const formData = reactive({
  first_name: '',
  last_name: '',
  email_address: '',
  phone_number: '',
  country_id: '',
  state_id: '',
  city_id: '',
  zip_code: '',
  area_apartment: '',
  address: '',
  delivery_charge: '',
  sub_total: 0,
  discount: 0,
  tax_amount: 0,
  total: 0,
  cart_details: [],
  coupon_code: ''
});

const validateForm = () => {
  const requiredFields = [
    'first_name',
    'last_name',
    'email',
    'phone',
    'country_id',
    'state_id',
    'city_id',
    'zip_code',
    'area_apartment',
    'delivery_charge',
    'address'
  ];
  let isValid = true;

  requiredFields.forEach((field) => {
    const inputElement = document.getElementById(field);
    const errorElement = document.getElementById(`${field}_error`);
    if (!inputElement.value) {
      inputElement.classList.add('is-invalid');
      errorElement.textContent = `The ${formatLabel(field)} is required.`;
      isValid = false;
    } else {
      inputElement.classList.remove('is-invalid');
      errorElement.textContent = '';
    }
  });

  return isValid;
};

const payNow = () => {
  if (!validateForm()) return;

  if (!store.getUserDetails || store.getUserDetails()?.name === '') {
    router.push({ name: 'LoginPage' });
    return;
  }

  formData.sub_total = grandTotal.value;
  formData.total = discountedPrice.value || grandTotal.value;

  request.post('checkout/store', formData)
      .then(response => {
        store.clearCartDetails();
        toastr.success('Order placed successfully', 'Success');
        router.push({ name: 'OrderComplete' });
      })
}

const getCheckoutDetails = () => {
  request.get('checkout')
      .then(response => {
        countries.value = response.data.data?.countries
        deliveryCharges.value = response.data.data?.deliveryCharges
      })
}

const fetchStates = async () => {
  formData.state_id = '';
  formData.city_id = '';
  try {
    const response = await request.get(`/states?country_id=${formData.country_id}`);
    states.value = response.data;
    cities.value = [];
  } catch (error) {
    console.error("Error fetching states:", error);
  }
};

const fetchCities = async () => {
  formData.city_id = '';
  try {
    const response = await request.get(`/cities?state_id=${formData.state_id}`);
    cities.value = response.data;
  } catch (error) {
    console.error("Error fetching cities:", error);
  }
};

onMounted(() => {
  getCheckoutDetails();
  cartDetails.value = store.getCartDetails()
  formData.cart_details = store.getCartDetails()?.cartItems
})

const increaseQuantity = (cartData) => {
  cartData.qty++
  store.updateQuantity(cartData.rowId, cartData.qty)
  cartDetails.value = store.getCartDetails()
  formData.cart_details = store.getCartDetails()?.cartItems
}

const decreaseQuantity = (cartData) => {
  if (cartData.qty > 1) {
    cartData.qty--
    store.updateQuantity(cartData.rowId, cartData.qty)
    cartDetails.value = store.getCartDetails()
    formData.cart_details = store.getCartDetails()?.cartItems
  }
}

const removeCartData = (cartData) => {
  store.removeCartDetails(cartData.rowId)
  cartDetails.value = store.getCartDetails()
  formData.cart_details = store.getCartDetails()?.cartItems
}

const applyCoupon = () => {
  couponError.value = '';
  request.get('coupon/check', {
    params: {
      coupon_code: formData.coupon_code,
      order_amount: grandTotal.value
    }
  })
      .then(response => {
        formData.discount = response.data.data.discount_amount;
        discountedPrice.value = response.data.data.grand_total;
      })
      .catch(error => {
        formData.discount = 0;
        discountedPrice.value = 0;
        couponError.value = error.response.data.message;
      });
}

const removeCoupon = () => {
  couponError.value = '';
  formData.coupon_code = '';
  formData.discount = 0;
  discountedPrice.value = 0;
}

const totalAmount = computed(() => {
  let total = 0;
  if (cartDetails.value && cartDetails.value?.cartItems) {
    for (let key in cartDetails.value.cartItems) {
      const item = cartDetails.value.cartItems[key];
      total += parseFloat(item.subtotal);
    }
  }
  return total;
});

const grandTotal = computed(() => {
  return totalAmount.value
})

const formatLabel = (input) => {
  return input
      .replace(/_/g, ' ')
      .replace(/\b\w/g, (char) => char);
};
</script>

<style scoped>
.container {
  max-width: 600px;
}

@media (max-width: 768px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

h5, h6 {
  font-weight: bold;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.remove-coupon {
  margin-left: 5px !important;
}
</style>