<template>
  <div class="container my-5">
    <!-- Back Button -->
    <div class="d-flex justify-content-start mb-3">
      <router-link to="/" class="btn btn-light">
        <i class="bi bi-arrow-left"></i>
      </router-link>
      <h5 class="mb-0 flex-grow-1 text-center">Products | {{ product.title }}</h5>
    </div>

    <!-- Product Details -->
    <div class="row">
      <!-- Product Image and 360 View -->
      <div class="col-md-6 text-center">
        <div class="position-relative">
          <!-- Product Image -->
          <img :src="product.image_url" class="img-fluid" :alt="product.title" />
        </div>

        <!-- Color Options -->
        <!--<div class="d-flex justify-content-center my-3">
          <div
              v-for="(color, index) in colors"
              :key="index"
              class="rounded-circle mx-2"
              :style="{ backgroundColor: color, width: '25px', height: '25px', border: '2px solid white', cursor: 'pointer' }"
          ></div>
        </div>-->
      </div>

      <!-- Product Information -->
      <div class="col-md-6 mt-2">
        <h6 class="text-uppercase text-success">{{ product.product_type }}</h6>
        <h1 class="display-6">{{ product.title }}</h1>

        <!-- Ratings and Store Link -->
        <div class="d-flex align-items-center mb-3">
          <div>
            <i
                v-for="i in 5"
                :key="i"
                :class="i <= averageRating ? 'bi bi-star-fill text-warning' : 'bi bi-star text-warning'"
            ></i>
            <span v-if="averageRating > 0">({{ averageRating }})</span>
          </div>
        </div>

        <h5 class="text-success mb-3">৳{{ product.price }}</h5>

        <!-- Color Selection -->
        <h5>Select Color</h5>
        <div class="d-flex flex-wrap">
          <label class="custom-radio-button color-radio mb-0" v-for="(color, index) in colors" :key="index">
            <input
                type="radio"
                name="color"
                :value="color"
                v-model="selectedColor"
                class="d-none"
                @change="handleChangeColor"
            />
            <span class="color-square">{{ color }}</span>
          </label>
        </div>
        <span v-if="colorError" class="text-danger">Please select a color</span>

        <!-- Size Selection -->
        <h5 class="mt-3">Select Size</h5>
        <div class="d-flex flex-wrap">
          <label class="custom-radio-button size-radio mb-0" v-for="(size, index) in sizes" :key="index">
            <input
                type="radio"
                name="size"
                :value="size"
                v-model="selectedSize"
                class="d-none"
                @change="handleChangeSize"
            />
            <span class="size-square">{{ size }}</span>
          </label>
        </div>
        <span v-if="sizeError" class="text-danger">Please select a size</span>

        <div class="mt-3">
          <button @click.prevent="addToCart" class="btn btn-primary addToCartBtn">Add To Cart</button>
          <button class="btn btn-outline-danger ms-2" @click="addToWishlist">Add to Wishlist</button>
        </div>

        <!-- Product Gallery -->
        <div class="gallery d-flex mt-3 mb-3">
          <img
              v-for="(img, index) in product.all_image_url"
              :key="index"
              :src="img"
              alt="product"
              class="rounded me-2"
              style="width: 70px; height: 70px; object-fit: cover; cursor: pointer;"
          />
        </div>

        <!-- Tabs for Description, Specification, Reviews -->
        <ul class="nav nav-tabs mb-3" id="productTab" role="tablist">
          <li class="nav-item" role="presentation">
            <button class="nav-link active" id="description-tab" data-bs-toggle="tab" data-bs-target="#description" type="button" role="tab">Description</button>
          </li>
          <li class="nav-item" role="presentation">
            <button class="nav-link" id="specification-tab" data-bs-toggle="tab" data-bs-target="#specification" type="button" role="tab">Specification</button>
          </li>
          <li class="nav-item" role="presentation">
            <button class="nav-link" id="reviews-tab" data-bs-toggle="tab" data-bs-target="#reviews" type="button" role="tab">Reviews</button>
          </li>
        </ul>

        <!-- Tab Content -->
        <div class="tab-content" id="productTabContent">
          <!-- Description Tab -->
          <div class="tab-pane fade show active" id="description" role="tabpanel" aria-labelledby="description-tab">
            <p>
              {{ product.short_description }}
            </p>
          </div>

          <!-- Specification Tab -->
          <div class="tab-pane fade" id="specification" role="tabpanel" aria-labelledby="specification-tab">
            <p>
              {{ product.description }}
            </p>
          </div>

          <!-- Reviews Tab -->
          <div class="tab-pane fade" id="reviews" role="tabpanel" aria-labelledby="reviews-tab">
            <div v-if="loading" class="text-center">Loading reviews...</div>
            <div v-else>
              <!-- Display Reviews -->
              <div v-if="reviews.length">
                <h5>Customer Reviews</h5>
                <ul class="list-group">
                  <li v-for="review in reviews" :key="review.id" class="list-group-item">
                    <strong>{{ review.name }}</strong> - <small>{{ review.created_at }}
                    <i
                      v-for="i in 5"
                      :key="i"
                      :class="i <= review.rating ? 'bi bi-star-fill text-warning' : 'bi bi-star text-warning'"
                  ></i></small>
                    <p>{{ review.title }}</p>
                    <p>{{ review.comment }}</p>
                  </li>
                </ul>
              </div>
              <div v-else>
                <p>No reviews yet. Be the first to review!</p>
              </div>

              <!-- Add Review -->
              <h5 class="mt-4">Add Your Review</h5>
              <form @submit.prevent="submitReview">
                <div class="row justify-content-center">
                  <!-- Name Input -->
                  <div class="col-lg-6 col-md-6">
                    <div class="form-group">
                      <label for="name">Name <span class="text-danger">*</span></label>
                      <input
                          type="text"
                          id="name"
                          v-model="form.name"
                          placeholder="Enter your name"
                          class="form-control"
                          :class="{ 'is-invalid': errors.name }"
                      />
                      <span v-if="errors.name" class="text-danger">{{ errors.name }}</span>
                    </div>
                  </div>

                  <!-- Email Input -->
                  <div class="col-lg-6 col-md-6 mt-2">
                    <div class="form-group">
                      <label for="email">Email <span class="text-danger">*</span></label>
                      <input
                          type="email"
                          id="email"
                          v-model="form.email"
                          placeholder="Enter your email"
                          class="form-control"
                          :class="{ 'is-invalid': errors.email }"
                      />
                      <span v-if="errors.email" class="text-danger">{{ errors.email }}</span>
                    </div>
                  </div>

                  <!-- Title Input -->
                  <div class="col-lg-12 col-md-12 mt-2">
                    <div class="form-group">
                      <label for="review_title">Title <span class="text-danger">*</span></label>
                      <input
                          type="text"
                          id="review_title"
                          v-model="form.title"
                          placeholder="Enter your review title"
                          class="form-control"
                          :class="{ 'is-invalid': errors.title }"
                      />
                      <span v-if="errors.title" class="text-danger">{{ errors.title }}</span>
                    </div>
                  </div>

                  <!-- Comment Input -->
                  <div class="col-lg-12 col-md-12 mt-2">
                    <div class="form-group">
                      <label for="comment">Comment <span class="text-danger">*</span></label>
                      <textarea
                          id="comment"
                          v-model="form.comment"
                          cols="30"
                          rows="2"
                          placeholder="Write your comments here"
                          class="form-control"
                          :class="{ 'is-invalid': errors.comment }"
                      ></textarea>
                      <span v-if="errors.comment" class="text-danger">{{ errors.comment }}</span>
                    </div>
                  </div>

                  <!-- Star Rating -->
                  <div class="col-lg-12 col-md-12 mt-2">
                    <div class="form-group star-rating">
                      <label for="rating">Rate your experience</label>
                      <div class="star-wrapper">
                        <input
                            type="radio"
                            name="rating"
                            id="star5"
                            value="5"
                            v-model="form.rating"
                        />
                        <label for="star5" title="5 stars">★</label>
                        <input
                            type="radio"
                            name="rating"
                            id="star4"
                            value="4"
                            v-model="form.rating"
                        />
                        <label for="star4" title="4 stars">★</label>
                        <input
                            type="radio"
                            name="rating"
                            id="star3"
                            value="3"
                            v-model="form.rating"
                        />
                        <label for="star3" title="3 stars">★</label>
                        <input
                            type="radio"
                            name="rating"
                            id="star2"
                            value="2"
                            v-model="form.rating"
                        />
                        <label for="star2" title="2 stars">★</label>
                        <input
                            type="radio"
                            name="rating"
                            id="star1"
                            value="1"
                            v-model="form.rating"
                        />
                        <label for="star1" title="1 star">★</label>
                      </div>
                      <span v-if="errors.rating" class="text-danger">{{ errors.rating }}</span>
                    </div>
                  </div>

                  <!-- Submit Button -->
                  <div class="col-lg-12 col-md-12">
                    <button type="submit" class="btn btn-primary">Submit Review</button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { useRoute } from 'vue-router';
import {request} from "@/helpers/request";
import {onMounted, reactive, ref} from "vue";
import router from "@/router";
import {useStore} from "@/stores/mutations";
import toastr from 'toastr';
import {useAuthStore} from "@/stores/auth";
const route = useRoute();
const productId = route.params.id;
const averageRating = ref(0)
const colors = ref([])
const sizes = ref([])
const product = ref([])
const relatedProducts = ref([])
const selectedColor = ref(null);
const selectedSize = ref(null);
const colorError = ref(false);
const sizeError = ref(false);
const authStore = useAuthStore()
const reviews = ref([]);
const loading = ref(false);
const form = reactive({
  name: "",
  email: "",
  title: "",
  comment: "",
  rating: null,
  product_id: productId
});

const errors = ref({});

const validateForm = () => {
  errors.value = {};

  if (!form.name.trim()) {
    errors.value.name = "Name is required.";
  }

  if (!form.email.trim()) {
    errors.value.email = "Email is required.";
  } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(form.email)) {
    errors.value.email = "Email must be a valid email address.";
  }

  if (!form.title.trim()) {
    errors.value.title = "Title is required.";
  }

  if (!form.comment.trim()) {
    errors.value.comment = "Comment is required.";
  } else if (form.comment.length < 5) {
    errors.value.comment = "Comment must be at least 5 characters long.";
  }

  if (!form.rating) {
    errors.value.rating = "Rating is required.";
  }

  return Object.keys(errors.value).length === 0;
};

const fetchReviews = async () => {
  try {
    const response = await request.get('review', {
      params: {
        product_id: productId
      }
    });

    reviews.value = response?.data?.data;
  } catch (error) {
    console.error("Error fetching reviews:", error);
  } finally {
    loading.value = false;
  }
};

const submitReview = async () => {
  if (validateForm()) {
    try {
      const response = await request.post('review/store', form);
      fetchReviews();
      form.name = "";
      form.email = "";
      form.title = "";
      form.comment = "";
      form.rating = null;
      toastr.success('Review submitted successfully', 'Success');
    } catch (error) {
      console.error("Error submitting review:", error);
    }
  } else {
    //console.error("Validation failed:", errors.value);
  }
};

const getSingleProduct = () => {
  request.get('products/single-product/' + productId)
      .then(response => {
        averageRating.value = response.data.data?.averageRating
        colors.value = response.data.data?.colors
        sizes.value = response.data.data?.sizes
        product.value = response.data.data?.product
        relatedProducts.value = response.data.data?.relatedProducts
      })
      .catch(error => {
        console.log(error)
      });
}

const addToCart = () => {
  if (!selectedColor.value && !selectedSize.value) {
    colorError.value = true;
    sizeError.value = true;
    return;
  }

  if (!selectedColor.value) {
    colorError.value = true;
    return;
  }

  if (!selectedSize.value) {
    sizeError.value = true;
    return;
  }

  request.post('cart/add', {
      id: productId,
      name: product.value?.title,
      quantity: 1,
      price: product.value?.price,
      image: product.value?.image_url,
      color: selectedColor.value,
      size: selectedSize.value
    })
      .then(response => {
        console.log(response.data)
        useStore().setCartDetails(response.data)
        toastr.success('Product added to cart', 'Success');
        router.push('/cart/details')
      })
      .catch(error => {
        console.log(error)
      });
}

function addToWishlist() {
  let isAuthenticated = authStore.getAuthenticatedStatus();
  if (!isAuthenticated) {
    toastr.error('Please login to add product to wishlist', 'Error');
    return;
  }

  if (!selectedColor.value && !selectedSize.value) {
    colorError.value = true;
    sizeError.value = true;
    return;
  }

  if (!selectedColor.value) {
    colorError.value = true;
    return;
  }

  if (!selectedSize.value) {
    sizeError.value = true;
    return;
  }

  request.post('wishlist/add', {
    product_id: product.value?.id,
    qty: 1,
    color: selectedColor.value,
    size: selectedSize.value
  })
      .then(response => {
        toastr.success('Product added to wishlist', 'Success');
        router.push('/wishlist/details')
      })
      .catch(error => {
        toastr.error(error?.response?.data?.error, 'Error');
      });
}

const handleChangeColor = () => {
  if (!selectedColor.value) {
    colorError.value = true;
  } else {
    colorError.value = false;
  }
}

const handleChangeSize = () => {
  if (!selectedSize.value) {
    sizeError.value = true;
  } else {
    sizeError.value = false;
  }
}

onMounted(() => {
    getSingleProduct();
    fetchReviews();
})
</script>

<style scoped>
.star-wrapper {
  display: flex;
  gap: 5px;
  direction: rtl;
}

.star-wrapper input {
  display: none;
}

.star-wrapper label {
  font-size: 2rem;
  cursor: pointer;
  color: gray;
  transition: color 0.2s ease;
}

.star-wrapper input:checked ~ label {
  color: gold;
}

.star-wrapper label:hover,
.star-wrapper label:hover ~ label {
  color: gold;
}
</style>