<template>
  <div class="d-flex justify-content-between align-items-center p-3 bg-light">
    <!-- Profile Section -->
    <div class="d-flex align-items-center">
      <img
          :src="profilePhoto"
          alt="Profile Picture"
          class="rounded-circle me-2"
          width="50"
          height="50"
      />
      <div>
        <h5 class="mb-0">Welcome,</h5>
        <h6 class="fw-bold">{{ store.getUserDetails().name }}</h6>
      </div>
    </div>

    <!-- Notification Icon -->
    <div class="d-flex align-items-center">
      <router-link :to="{name: 'NotificationDetails'}">
        <i class="bi bi-bell position-relative">
        <span
            class="position-absolute top-0 start-100 translate-middle p-1 bg-success border border-light rounded-circle"
            style="width: 8px; height: 8px;"
        ></span>
        </i>
      </router-link>
    </div>
  </div>

  <!-- Search Section -->
  <div class="p-3 bg-light d-flex align-items-center">
    <div class="input-group">
      <span class="input-group-text bg-transparent border-0">
        <i class="bi bi-search"></i>
      </span>
      <input type="text" v-model="searchQuery" class="form-control border-0" placeholder="Search" />
      <!--<span class="input-group-text bg-transparent border-0">
        <i class="bi bi-sliders"></i>
      </span>-->
    </div>
  </div>

  <!-- Search Results -->
  <div v-if="searchQuery && searchResults.length > 0" class="p-3">
    <h6>Search Results:</h6>
    <div v-for="result in searchResults" :key="result.id" class="search-result-item">
      <router-link :to="{ name: 'SingleProduct', params: { id: result.id } }" class="text-decoration-none">
        <div class="d-flex align-items-center">
          <img :src="result.image_url" alt="product.name" class="me-2" style="width: 50px; height: 50px;" />
          <div>
            <h6 class="mb-0">{{ result.title }}</h6>
            <small class="text-muted">{{ formatPrice(result.price) }}</small>
          </div>
        </div>
      </router-link>
    </div>
  </div>

  <div v-else-if="isSearching" class="p-3">
    <p>Searching...</p>
  </div>

  <div id="container">
    <div id="carouselExampleIndicators" class="carousel slide p-3" data-bs-ride="carousel">
      <!-- Indicators (Optional) -->
      <div class="carousel-indicators">
        <button
            v-for="(slider, index) in sliders"
            :key="index"
            type="button"
            data-bs-target="#carouselExampleIndicators"
            :data-bs-slide-to="index"
            :class="{ active: index === activeSlide }"
            :aria-current="index === activeSlide ? 'true' : ''"
            :aria-label="'Slide ' + (index + 1)"
            @click="setActiveSlide(index)"
        ></button>
      </div>

      <!-- Slider Images -->
      <div class="carousel-inner">
        <div
            v-for="(slider, index) in sliders"
            :key="slider.id"
            :class="['carousel-item', { active: index === activeSlide }]"
        >
          <img :src="slider.image" class="d-block w-100" :alt="slider.title" />
        </div>
      </div>

      <!-- Controls (Optional) -->
      <button
          class="carousel-control-prev"
          type="button"
          data-bs-target="#carouselExampleIndicators"
          data-bs-slide="prev"
          @click="prevSlide"
      >
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="visually-hidden">Previous</span>
      </button>
      <button
          class="carousel-control-next"
          type="button"
          data-bs-target="#carouselExampleIndicators"
          data-bs-slide="next"
          @click="nextSlide"
      >
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="visually-hidden">Next</span>
      </button>
    </div>

    <div class="mt-2 p-3 mb-5">
      <!-- Categories Section -->
      <div class="d-flex justify-content-between align-items-center">
        <h5>Categories</h5>
        <router-link to="/products/categories" class="text-decoration-none see-all-btn">See all</router-link>
      </div>

      <div class="category-section mb-4" role="group" aria-label="Category Buttons">
        <button v-for="(category, index) in categories" :key="category.id" type="button"
                :class="{
                     'active category btn btn-outline-primary d-flex align-items-center': selectedCategoryIndex === index,
                      'category btn btn-outline-primary d-flex align-items-center': selectedCategoryIndex !== index
                }"
                @click="selectCategory(index)"
        >
          <router-link :to="{ name: 'ShopByCategory', params: { slug: category.slug } }" class="text-decoration-none text-dark">
            <img :src="category.image_url" alt="Armchair" class="me-2" style="width: 30px; height: 30px;">
            <span>{{ category.category_name }}</span>
          </router-link>
        </button>
      </div>

      <!-- Recent Products Section -->
      <div class="d-flex justify-content-between align-items-center mb-3">
        <h5>Recent Products</h5>
        <router-link to="/products" class="text-decoration-none see-all-btn">See all</router-link>
      </div>

      <!-- Products Grid -->
      <div class="row">
        <div class="col-6 col-md-4 col-lg-3 mb-4" v-for="recentProduct in recentProducts" :key="recentProduct.id">
          <div class="card product-card h-100">
            <router-link :to="{ name: 'SingleProduct', params: { id: recentProduct.id } }" class="text-decoration-none">
                <img :src="recentProduct.image_url" class="card-img-top" alt="product.name">
            </router-link>
            <div class="card-body">
              <router-link :to="{ name: 'SingleProduct', params: { id: recentProduct.id } }" class="text-decoration-none">
                <h6 class="text-success">{{ recentProduct.product_type }}</h6>
                <h5 class="card-title text-dark">{{ recentProduct.title }}</h5>
                <p class="card-text">{{ formatPrice(recentProduct.price) }}</p>
              </router-link>
            </div>
            <div class="d-flex justify-content-end align-items-center">
              <button class="btn btn-primary plus-btn" @click="openModal(recentProduct)"><i class="bi bi-plus"></i></button>
            </div>
          </div>
        </div>
      </div>

      <!-- Popular Products Section -->
      <div class="d-flex justify-content-between align-items-center mb-3">
        <h5>Popular Products</h5>
        <router-link to="/products" class="text-decoration-none see-all-btn">See all</router-link>
      </div>

      <!-- Products Grid -->
      <div class="row">
        <div class="col-6 col-md-4 col-lg-3 mb-4" v-for="popularProduct in popularProducts" :key="popularProduct.id">
          <div class="card product-card h-100">
            <router-link :to="{ name: 'SingleProduct', params: { id: popularProduct.id } }" class="text-decoration-none">
              <img :src="popularProduct.image_url" class="card-img-top" alt="product.name">
            </router-link>
            <div class="card-body">
              <router-link :to="{ name: 'SingleProduct', params: { id: popularProduct.id } }" class="text-decoration-none">
                <h6 class="text-success">{{ popularProduct.product_type }}</h6>
                <h5 class="card-title text-dark">{{ popularProduct.title }}</h5>
                <p class="card-text">{{ formatPrice(popularProduct.price) }}</p>
              </router-link>
            </div>
            <div class="d-flex justify-content-end align-items-center">
              <button class="btn btn-primary plus-btn" @click="openModal(popularProduct)"><i class="bi bi-plus"></i></button>
            </div>
          </div>
        </div>
      </div>

      <!-- Best Selling Products Section -->
      <div class="d-flex justify-content-between align-items-center mb-3">
        <h5>Best Selling Products</h5>
        <router-link to="/products" class="text-decoration-none see-all-btn">See all</router-link>
      </div>

      <!-- Products Grid -->
      <div class="row">
        <div class="col-6 col-md-4 col-lg-3 mb-4" v-for="bestSellingProduct in bestSellingProducts" :key="bestSellingProduct.id">
          <div class="card product-card h-100">
            <router-link :to="{ name: 'SingleProduct', params: { id: bestSellingProduct.id } }" class="text-decoration-none">
              <img :src="bestSellingProduct.image_url" class="card-img-top" alt="product.name">
            </router-link>
            <div class="card-body">
              <router-link :to="{ name: 'SingleProduct', params: { id: bestSellingProduct.id } }" class="text-decoration-none">
                <h6 class="text-success">{{ bestSellingProduct.product_type }}</h6>
                <h5 class="card-title text-dark">{{ bestSellingProduct.title }}</h5>
                <p class="card-text">{{ formatPrice(bestSellingProduct.price) }}</p>
              </router-link>
            </div>
            <div class="d-flex justify-content-end align-items-center">
              <button class="btn btn-primary plus-btn" @click="openModal(bestSellingProduct)"><i class="bi bi-plus"></i></button>
            </div>
          </div>
        </div>
      </div>

      <!-- Brands Section -->
      <div class="d-flex justify-content-between align-items-center">
        <h5>Brands</h5>
        <router-link to="/products/brands" class="text-decoration-none see-all-btn">See all</router-link>
      </div>

      <div class="brand-section mb-4" role="group" aria-label="Brand Buttons">
        <button v-for="(brand, index) in brands" :key="brand.id" type="button"
                :class="{
                     'active brand btn btn-outline-primary d-flex align-items-center': selectedBrandIndex === index,
                      'brand btn btn-outline-primary d-flex align-items-center': selectedBrandIndex !== index
                }"
                @click="selectBrand(index)"
        >
          <router-link :to="{ name: 'ShopByBrand', params: { slug: brand.slug } }" class="text-decoration-none text-dark">
            <img :src="brand.image_url" :alt="brand.brand_name" class="me-2" style="width: 30px; height: 30px;">
            <span>{{ brand.brand_name }}</span>
          </router-link>
        </button>
      </div>
    </div>
  </div>

  <QuickViewModal
      v-if="selectedProduct"
      :product="selectedProduct"
      @close="selectedProduct = null"
  />
  <FooterPage />
</template>

<script setup>
import FooterPage from "@/views/FooterPage.vue";
import {useStore} from "@/stores/mutations";
import {onMounted, onUnmounted, ref, watch} from "vue";
import {request} from "@/helpers/request";
import QuickViewModal from "@/views/modal/QuickViewModal.vue";
const store = useStore()
const sliders = ref([])
const bestSellingProducts = ref([])
const popularProducts = ref([])
const recentProducts = ref([])
const categories = ref([])
const brands = ref([])
const activeSlide = ref(0);
const selectedCategoryIndex = ref(0);
const selectedBrandIndex = ref(0);
const selectedProduct = ref(null);
const searchQuery = ref("");
const searchResults = ref([]);
const isSearching = ref(false);
const profilePhoto = ref(store.getUserDetails()?.profile_photo ?? 'https://via.placeholder.com/50');

const selectCategory = (index) => {
  selectedCategoryIndex.value = index;
};

const selectBrand = (index) => {
  selectedBrandIndex.value = index;
};

function openModal(product) {
  selectedProduct.value = product;
}

const getSliders = () => {
  request.get('sliders')
      .then(response => {
        sliders.value = response.data.data
      })
      .catch(error => {
        console.log(error)
      });
}

const getProducts = () => {
  request.get('products/all')
      .then(response => {
        bestSellingProducts.value = response.data.data?.bestSellingProducts
        popularProducts.value = response.data.data?.popularProducts
        recentProducts.value = response.data.data?.recentProducts
        categories.value = response.data.data?.categories
        brands.value = response.data.data?.brands
      })
      .catch(error => {
        console.log(error)
      });
}

const setActiveSlide = (index) => {
  activeSlide.value = index;
};

const prevSlide = () => {
  activeSlide.value = (activeSlide.value - 1 + sliders.value.length) % sliders.value.length;
};

const nextSlide = () => {
  activeSlide.value = (activeSlide.value + 1) % sliders.value.length;
};

// Automatic sliding logic
let slideInterval;
const startAutoSlide = () => {
  slideInterval = setInterval(() => {
    nextSlide();
  }, 3000); // Change slide every 3 seconds
};

const stopAutoSlide = () => {
  clearInterval(slideInterval);
};

const performSearch = () => {
  if (searchQuery.value.length >= 2) {
    isSearching.value = true;
    request.get(`products/search`, { params: { search: searchQuery.value } })
        .then(response => {
          searchResults.value = response.data.data;
          isSearching.value = false;
        })
        .catch(error => {
          console.error("Search Error:", error);
          isSearching.value = false;
        });
  } else {
    searchResults.value = [];
  }
};

watch(searchQuery, performSearch);

onMounted(() => {
  getSliders();
  startAutoSlide();
  getProducts();
})

onUnmounted(() => {
  stopAutoSlide();
});

const formatPrice = (price) => {
  return `${price.toFixed(2)}`;
};
</script>

