        <img :src="logoPreview" alt="Profile Image" class="rounded-circle me-3" width="100" height="100">

        <div>
          <h5 class="mb-1">{{ store.getUserDetails().name }}</h5>
          <p class="text-muted mb-2">{{ store.getUserDetails().email }}</p>
          <router-link :to="{name: 'EditProfile'}" class="btn btn-primary">Edit Profile</router-link>
        </div>
      </div>

      <div class="list-group list-group-flush text-start mt-4">
        <!--<a href="#" class="list-group-item list-group-item-action d-flex align-items-center">
          <i class="bi bi-wallet2 me-3"></i> Payment Methods
        </a>-->
        <router-link :to="{name: 'OrderHistory'}" class="list-group-item list-group-item-action d-flex align-items-center">
          <i class="bi bi-bag me-3"></i> Order History
        </router-link>
        <router-link :to="{name: 'WishlistDetails'}" class="list-group-item list-group-item-action d-flex align-items-center">
