<script setup>
import { request } from "@/helpers/request";
import { removeToken } from "@/helpers/auth";
import { useRouter } from "vue-router";
import { useStore } from "@/stores/mutations";
import { useAuthStore } from "@/stores/auth";
import {ref} from "vue";
import toastr from 'toastr';
const router = useRouter();
const store = useStore();
const authStore = useAuthStore();

const logoPreview = ref(store.getUserDetails()?.profile_photo ?? 'https://via.placeholder.com/100');

const logout = async () => {
  await request.post("logout")
      .then((response) => {
        removeToken();
        store.removeToken();
        authStore.logout();
        authStore.removeAuthenticatedStatus();
        toastr.success('Logging out success', 'Success');
        router.push("/");
      })
      .catch((error) => {
        console.log(error);
      });
};
</script>

