<template>
  <div class="container py-4">
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link :to="{name: 'OrderHistory'}" class="btn btn-light icon-circle me-3">
          <i class="bi bi-chevron-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center">Order #{{ orderId }}</h3>
      </div>
    </div>

    <!-- Orders List -->
    <div class="orders-list">
      <div class="order-card mb-4 p-3 border rounded">
        <h5>Invoice #{{ orderId }}</h5>
        <p><strong>Customer:</strong> {{ order.fullname }}</p>
        <p><strong>Status:</strong> <span :class="getStatusBadge(order.order_status)">{{ getStatusText(order.order_status) }}</span></p>
        <p><strong>Courier Status:</strong> {{ order.courier_status }}</p>
        <p><strong>Created At:</strong> {{ new Date(order.created_at).toLocaleDateString() }}</p>
      </div>

      <div class="order-card mb-4 p-3 border rounded">
        <h6>Order Summary</h6>
        <div class="d-flex justify-content-between">
          <span>Total Items</span>
          <span>{{ order.order_details?.length }} Items</span>
        </div>
        <div class="d-flex justify-content-between mt-1">
          <span>Sub Total</span>
          <span>৳{{ order.sub_total - order.delivery_charge }}</span>
        </div>
        <div class="d-flex justify-content-between mt-1">
          <span>Delivery Charge</span>
          <span>৳{{ order.delivery_charge }}</span>
        </div>
        <div v-if="order.discount > 0" class="d-flex justify-content-between mt-1">
          <span>Discount Amount</span>
          <span>৳{{ order.discount }}</span>
        </div>
        <div class="flex-grow-1 border-dotted mt-2 mb-2"></div>
        <div class="d-flex justify-content-between mt-1">
          <span class="fw-bold">Total Amount</span>
          <span class="fw-bold">৳{{ order.grand_total }}</span>
        </div>
      </div>

      <div v-for="details in order.order_details" :key="details.id" class="order-card mb-4 p-3 border rounded">
        <div class="d-flex justify-content-between">
          <span>
            <img v-if="details?.product?.product_image_url" :src="details?.product?.product_image_url" width="50px" alt="Product Photo">
            <img v-else src="https://via.placeholder.com/50" alt="Product Photo">
          </span>
          <span>
            <span class="fw-bold">{{ details?.product?.title }}</span>
            <br>
            Color: {{ details?.color }}, Size: {{ details?.size }}
          </span>
          <span>৳{{ details?.product?.price }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import {ref, onMounted} from "vue";
import {useRoute, useRouter} from "vue-router";
import {useStore} from "@/stores/mutations";
import {request} from "@/helpers/request";

const router = useRouter();
const store = useStore();
const order = ref([]);
const route = useRoute();
const orderId = route.params.id;

const fetchOrder = async () => {
  try {
    const response = await request.get("/dashboard/order/details", { params: { order_id: orderId } });
    order.value = response.data.data;
  } catch (error) {
    console.error("Error fetching orders:", error);
  }
};

const getStatusText = (status) => {
  switch (status) {
    case 0: return "Processing";
    case 1: return "Confirm";
    case 2: return "Shipped";
    case 3: return "Delivered";
    case 4: return "Cancelled";
    case 5: return "Success";
    default: return "Unknown";
  }
};

const getStatusBadge = (status) => {
  switch (status) {
    case 0: return "badge bg-warning";        // Processing
    case 1: return "badge bg-primary";        // Confirm
    case 2: return "badge bg-info";           // Shipped
    case 3: return "badge bg-success";        // Delivered
    case 4: return "badge bg-danger";         // Cancelled
    case 5: return "badge bg-success";        // Success
    default: return "badge bg-secondary";     // Unknown or other
  }
};

onMounted(() => {
  fetchOrder()
});
</script>

<style scoped>
.container {
  max-width: 500px;
  margin: auto;
  background-color: #f8f9fa;
  border-radius: 10px;
  padding: 20px;
}

.order-card {
  background-color: #ffffff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}

h1, h5 {
  font-weight: bold;
}
.border-dotted {
  border-bottom: 2px dotted rgba(0, 0, 0, 0.53);
}
</style>